.app_background {
  width: 100%;
  background-color: #f0f0f0;
  min-height: 1000px;
}

.app_container {
  width: 95%;
  height: 100%;
}

.page-width-limit {
  max-width: 800px;
  margin-left: auto;
  margin-right: auto;
}

.norm_padding, .landing-title, .landing-subtitle, .landing-welcome, .content-block, .content-block.summary > .summary-block-text {
  padding: 1em;
}

.header-text {
  flex-grow: 1;
}

.header-button {
  margin-left: 1em !important;
}

.landing-title {
  padding: 0.25em;
  background-color: #dd5542;
  color: white !important;
}

.landing-subtitle {
  padding: 0.25em;
  background-color: rgba(221, 85, 66, 0.75);
  color: white !important;
}

.landing-welcome {
  border-left: 3px solid #dd5542;
}

.landing-nav {
  position: sticky;
  top: 70px;
}

.landing-nav * a {
  padding: 0.5em;
  display: block;
  border-bottom: 0 !important;
  color: black;
  font-weight: normal;
  font-size: 12pt;
}

.landing-nav * a.is-active {
  color: #dd5542 !important;
}

.drawer-button {
  position: fixed !important;
  right: 0;
  top: 50%;
}

.page-outline-large {
  position: fixed;
  right: 0;
  top: 65px;
  width: 22%;
  z-index: 10000;
}

.first-block {
  margin-top: 10px !important;
}

.content-block {
  position: relative;
  margin-bottom: 2em !important;
  margin-top: 2em !important;
}

.content-block.summary > .summary-block-text {
  background-color: #f0f0f0 !important;
  width: 100%;
}

.content-block.summary > .summary-block-text.large {
  background-color: rgba(240, 240, 240, 0.75) !important;
  width: 50%;
  position: absolute;
  left: 10px;
  bottom: -80px;
}

.content-block.summary > .summary-block-text > h2 {
  color: #dd5542;
  font-weight: 600;
  border-bottom: 3px #dd5542 solid;
}

.content-block.summary > .summary-block-text .landing_text_block {
  background-color: rgba(240, 240, 240, 0.75) !important;
}

.content-block.summary > .summary-block-text .landing_text_block > h2 {
  color: #dd5542;
  font-weight: 600;
  border-bottom: 3px #dd5542 solid;
}

.content-block.summary > img {
  width: 100%;
  height: auto;
}

.content-block.summary > img.large {
  width: 97%;
  max-width: 800px;
  height: auto;
  right-margin: 1em;
}

.content-block.dark-bg {
  background-color: rgba(240, 240, 240, 0.75);
}

.limit_lg {
  max-width: 800px !important;
  height: auto;
}

.limit_md {
  max-width: 400px !important;
  height: auto;
}

.limit_sm {
  max-width: 200px !important;
  height: auto;
}

ol,
ul {
  padding-left: 20px;
}

a {
  font-style: normal;
  font-weight: bolder;
  color: #dd5542;
  border-bottom: 2px solid #dd5542;
  text-decoration: none !important;
}

a:hover {
  color: #dd5542;
}

.grid_img {
  width: 100%;
}

.supertitle {
  font-size: 10pt;
  color: gray;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 0.1em;
}

.inline-icon {
  margin: 3px;
  font-size: inherit !important;
  top: 5px;
  position: relative;
  transition: all 0.5s ease-in-out;
}

.inline-icon:hover {
  transform: scale(1.4);
}

.big-number {
  color: #dd5542;
  text-transform: capitalize;
  padding: 0.25em 0 0.24em;
  justify: center;
}

.rotate-180 {
  transition: all 0.70s ease;
  -webkit-transition: 0.70s;
  -moz-transition: 0.70s;
  -ms-transition: 0.70s;
  -o-transition: 0.70s;
}

.rotate-180.now {
  transition: all 0.70s ease;
  -webkit-transition: 0.70s;
  -moz-transition: 0.70s;
  -ms-transition: 0.70s;
  -o-transition: 0.70s;
  -webkit-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -o-transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  transform: rotate(180deg);
}

.slideshow-img {
  width: 100%;
  height: auto;
}

.slideshow-container {
  width: 100%;
}

table > thead > tr > th,
tbody > tr > th {
  font-weight: bold !important;
  font-size: 12pt !important;
  color: black !important;
  padding: 10px !important;
  text-align: center !important;
}

tbody > tr > td {
  padding: 10px !important;
  font-size: 12pt;
  justify: center;
}

table.options-table > tbody > tr > th {
  width: 10%;
}

table.options-table > tbody > tr > td {
  width: 30%;
}

.DialogTitle {
  text-transform: capitalize;
}
